import { onMounted } from 'vue';
import * as THREE from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { BufferGeometryUtils } from 'three/examples/jsm/utils/BufferGeometryUtils.js';
import { MeshBVH, MeshBVHVisualizer } from 'three-mesh-bvh';

export default function addMap(loader: GLTFLoader, scene: THREE.Scene, objects: any, mapPath: string, collisionMapPath: string) {
    onMounted( () => {

        loader.load(
            mapPath,
            gltf => {
                const model = gltf.scene;

                const box = new THREE.Box3();
                box.setFromObject(model);
                box.getCenter(model.position).negate();
                model.updateMatrixWorld(true);

                const toMerge = {};
                model.traverse(child => {
                    if (child instanceof THREE.Mesh) {
                        const hex = child.material.color.getHex();
                        toMerge[hex] = toMerge[hex] || [];
                        toMerge[hex].push(child);
                        child.castShadow = true;
                        child.receiveShadow = true;
                        child.material.shadowSide = 2;
                    }
                });

                const environment = new THREE.Group();
                for (const hex in toMerge) {
                    const arr = toMerge[hex];
                    const visualGeometries = [];
                    arr.forEach(mesh => {
                        if (mesh.material.emissive.r !== 0) {
                            environment.attach(mesh);
                        } else {
                            const geom = mesh.geometry.clone();
                            geom.applyMatrix4(mesh.matrixWorld);
                            visualGeometries.push(geom as never);
                        }
                    });

                    if (visualGeometries.length) {
                        const newGeom = BufferGeometryUtils.mergeBufferGeometries(visualGeometries);
                        const newMesh = new THREE.Mesh(newGeom, new THREE.MeshStandardMaterial({ color: 0xffffff, opacity: 0, transparent: true, depthWrite: false }));

                        newMesh.castShadow = true;
                        newMesh.receiveShadow = true;
                        newMesh.material.shadowSide = 2;

                        environment.add(newMesh);
                    }
                }

                // collect all geometries to merge
                const geometries = [];
                environment.updateMatrixWorld(true);
                environment.traverse(child=> {
                    if (child instanceof THREE.Mesh) {
                        const cloned = child.geometry.clone();
                        cloned.applyMatrix4( child.matrixWorld );
                        for (const key in cloned.attributes) {
                            if (key !== 'position') {
                                cloned.deleteAttribute(key);
                            }
                        }
                        geometries.push(cloned as never);
                    }
                });

                // create the merged geometry
                const mergedGeometry = BufferGeometryUtils.mergeBufferGeometries(geometries, false);
                (mergedGeometry as any).boundsTree = new MeshBVH(mergedGeometry, {lazyGeneration: false});

                const collider = new THREE.Mesh(mergedGeometry);
                (collider.material as THREE.MeshStandardMaterial).wireframe = true;
                (collider.material as THREE.MeshStandardMaterial).opacity = 0.5;
                (collider.material as THREE.MeshStandardMaterial).transparent = true;

                collider.userData.name = 'collider';
                objects[collider.userData.name] = collider;

                const visualizer = new MeshBVHVisualizer(collider, 1000);
                visualizer.visible = true
                scene.add(model);
                //scene.add(visualizer)
                //scene.add(collider);
                //scene.add(environment);
            },
            undefined,
            function(error) {
                console.error(error);
            },
        );

        loader.load(
            collisionMapPath,
            gltf => {
                const model = gltf.scene;
                model.userData.name = 'floor';
                model.visible = false;
                scene.add(model);

                //Zapisz dane obiektu
                objects[model.userData.name] = model; // Obiekt
            },
            undefined,
            function(error) {
                console.error(error);
            },
        );
    });
}