import { onMounted } from 'vue';
import * as THREE from 'three';

export default function addLights(scene: THREE.Scene) {

    onMounted( () => {
        const ambientLight = new THREE.HemisphereLight(0xffffff, 0x222222, 0.5);
        const mainLight = new THREE.DirectionalLight(0xffffff, 0.7);
        mainLight.position.set(1, 1.5, 1).multiplyScalar(100);
        mainLight.shadow.mapSize.setScalar(3072);
        mainLight.shadow.bias = -0.001;
        mainLight.shadow.normalBias = 0.05;
        mainLight.castShadow = true;

        const shadowCam = mainLight.shadow.camera;
        shadowCam.bottom = shadowCam.left = -300;
        shadowCam.top = 300;
        shadowCam.right = 300;

        // const mainLightHelper = new THREE.DirectionalLightHelper(mainLight, 5);
        // scene.add(mainLightHelper);

        // const mainLightCameraHelper = new THREE.CameraHelper(shadowCam);
        // scene.add(mainLightCameraHelper);

        scene.add(ambientLight, mainLight);
    });
}