
//import { Options, Vue } from 'vue-class-component';
import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer.js';
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass.js';
import { SMAAPass } from 'three/examples/jsm/postprocessing/SMAAPass.js';

import { ref, onMounted, onUnmounted } from 'vue'

export default {
    setup() {
        let camera!: THREE.PerspectiveCamera
        const renderer: THREE.WebGLRenderer = new THREE.WebGLRenderer({ antialias: true });
        const scene: THREE.Scene = new THREE.Scene();
        const clock = new THREE.Clock();
        const raycaster = new THREE.Raycaster();
        const objects: any = {};
        const mixers: any = {};
        const animations: any = {};
        const pressedKeys: any = {};
        const sceneRef = ref(null);

        const playAnimation = (objectMixer: any, objectAnimations: any, animationName: any, acceleration = 1, loop = true, weight = 1) => {
            const clip = objectAnimations.find((el: any) => el.name === animationName);
            const animation = objectMixer.clipAction(clip);
            animation.timeScale = acceleration;
            if (animations['Robot'][2].isPlaying) {
                weight = 0;
                setTimeout(() => {
                    playAnimation(objectMixer, objectAnimations, animationName, acceleration, true, 1);
                }, animations['Robot'][2].duration * 1000);
            }
            clip.isPlaying = true;
            if (!loop) {
                animation.setLoop(THREE.LoopOnce);
                animation
                    .reset()
                    .setEffectiveTimeScale(0.5)
                    .setEffectiveWeight(1) // wykombinować to ustawienie dla wszystkich animacji łączonych
                    .play();
            } else {
                animation.setEffectiveWeight(weight).play();
            }
        }

        const stopAnimation = (objectMixer: any, objectAnimations: any, animationName: any) => {
            const clip = objectAnimations.find((el: any) => el.name === animationName);
            const animation = objectMixer.clipAction(clip);
            animation.stop();
        }

        const testAllKeys = (keysArray: any, exceptionKeysArray: any = null) => {
            const activeInArray = keysArray.every((e: any) => pressedKeys[e] === true); // Czy klawisze z listy są wciąnięte
            if (exceptionKeysArray) {
                const exceptionInArray = exceptionKeysArray.every((e: any) => pressedKeys[e] === false || pressedKeys[e] === undefined);
                return activeInArray && exceptionInArray;
            }
            return activeInArray;
        };

        const testAnyKeys = (keysArray: any) => {
            return keysArray.some((e: any) => pressedKeys[e] === true);
        };

        const checkKeysState = () => {
            const robotPosition = new THREE.Vector3(objects['Robot'].position.x, objects['Robot'].position.y, objects['Robot'].position.z);
            const positionOffset = 5; // największa wysokość na jaką może wejść
            robotPosition.y += positionOffset;
            let speed = 0.18;
            const distanceLimit = 6; // limit wielkości na jaką może wejść/zejść
            let angle = 0;
            const move = new THREE.Vector3();

            if (testAllKeys(['ShiftLeft'])) {
                speed *= 2;
            }

            if (testAllKeys(['KeyW'], ['KeyA', 'KeyS', 'KeyD'])) {
                angle = 180;
                move.z = -1 * speed;
                robotPosition.z += -1 * speed;
            } else if (testAllKeys(['KeyA'], ['KeyW', 'KeyS', 'KeyD'])) {
                angle = -90;
                move.x = -1 * speed;
                robotPosition.x += -1 * speed;
            } else if (testAllKeys(['KeyS'], ['KeyW', 'KeyA', 'KeyD'])) {
                angle = 0;
                move.z = 1 * speed;
                robotPosition.z += 1 * speed;
            } else if (testAllKeys(['KeyD'], ['KeyW', 'KeyA', 'KeyS'])) {
                angle = 90;
                move.x = 1 * speed;
                robotPosition.x += 1 * speed;
            } else if (testAllKeys(['KeyW', 'KeyA'])) {
                angle = -135;
                move.x = -1 * speed;
                move.z = -1 * speed;
                robotPosition.x += -1 * speed;
                robotPosition.z += -1 * speed;
            } else if (testAllKeys(['KeyW', 'KeyD'])) {
                angle = 135;
                move.x = 1 * speed;
                move.z = -1 * speed;
                robotPosition.x += 1 * speed;
                robotPosition.z += -1 * speed;
            } else if (testAllKeys(['KeyS', 'KeyA'])) {
                angle = -45;
                move.x = -1 * speed;
                move.z = 1 * speed;
                robotPosition.x += -1 * speed;
                robotPosition.z += 1 * speed;
            } else if (testAllKeys(['KeyS', 'KeyD'])) {
                angle = 45;
                move.x = 1 * speed;
                move.z = 1 * speed;
                robotPosition.x += 1 * speed;
                robotPosition.z += 1 * speed;
            }

            // Wykrywanie kolizji
            raycaster.set(robotPosition, new THREE.Vector3(0, -1, 0));
            const intersects = raycaster.intersectObject(objects['floor'], true);

            // Pokazuje promienie raycastera
            //this.scene.add(new THREE.ArrowHelper(this.raycaster.ray.direction, this.raycaster.ray.origin, positionOffset, 0xff0000));

            if (intersects[0] && intersects[0].distance - positionOffset < distanceLimit) {
                move.y = -intersects[0].distance + positionOffset;
                return { move, angle };
            } else {
                move.x = 0;
                move.y = 0;
                move.z = 0;
                return { move, angle };
            }
        }

        const robotRunning = () => {
            if (mixers['Robot']) {
                if (testAnyKeys(['KeyW', 'KeyA', 'KeyS', 'KeyD'])) {
                    let acceleration = 1;
                    if (testAllKeys(['ShiftLeft'])) {
                        acceleration = 2;
                    }
                    playAnimation(mixers['Robot'], animations['Robot'], 'Robot_Running', acceleration);
                } else {
                    stopAnimation(mixers['Robot'], animations['Robot'], 'Robot_Running');
                }

                if (testAnyKeys(['Space'])) {
                    playAnimation(mixers['Robot'], animations['Robot'], 'Robot_Jump', 1, false);
                }
            }
        }

        const keyUp = (e: any) => {
            pressedKeys[e.code] = false;
            robotRunning();
        }

        const keyDown = (e: any) => {
            pressedKeys[e.code] = true;
            robotRunning();
        }

        //Created
        window.addEventListener('keydown', keyDown);
        window.addEventListener('keyup', keyUp);

        onMounted(() => {
            const el = sceneRef.value as any;
            camera = new THREE.PerspectiveCamera(60, el.clientWidth / el.clientHeight, 1, 1000);
            camera.position.set(55, 55, 55);
            renderer.setSize(el.clientWidth, el.clientHeight);
            el.appendChild(renderer.domElement);

            // Dodanie loadera plików
            let mixer: any;
            const loader = new GLTFLoader();

            loader.load(
                '/peach_castle/scene.gltf',
                gltf => {
                    const model = gltf.scene;
                    scene.add(model);

                    //Animacja
                    if (gltf.animations.length) {
                        mixer = new THREE.AnimationMixer(model);
                        mixer.clipAction(gltf.animations[0]).play();
                    }
                },
                undefined,
                function(error) {
                    console.error(error);
                },
            );

            loader.load(
                '/Robot.gltf',
                gltf => {
                    const model = gltf.scene;
                    model.userData.name = 'Robot';
                    scene.add(model);
                    model.position.set(5, 12, 0);

                    //Zapisz dane obiektu
                    objects[model.userData.name] = model; // Obiekt
                    animations[model.userData.name] = gltf.animations; // Animacja
                    animations[model.userData.name].forEach((e: any) => {
                        e.isPlaying = false;
                    });
                    mixers[model.userData.name] = new THREE.AnimationMixer(model); // Mikser animacji
                    mixers[model.userData.name].addEventListener('finished', function(e: any) {
                        e.action._clip.isPlaying = false;
                        console.log('animacja skonczona');
                    });
                },
                undefined,
                function(error) {
                    console.error(error);
                },
            );

            loader.load(
                '/peach_castle/floor.gltf',
                gltf => {
                    const model = gltf.scene;
                    model.userData.name = 'floor';
                    model.visible = false;
                    scene.add(model);

                    //Zapisz dane obiektu
                    objects[model.userData.name] = model; // Obiekt
                },
                undefined,
                function(error) {
                    console.error(error);
                },
            );

            // Dodanie światła
            const ambientLight = new THREE.HemisphereLight(0xffffff, 0x222222, 1);
            const mainLight = new THREE.DirectionalLight(0xffffff, 4.0);
            mainLight.position.set(10, 10, 10);
            scene.add(ambientLight, mainLight);

            // Sterowanie kamera za pomoca myszy
            const controls = new OrbitControls(camera, renderer.domElement);
            controls.enableDamping = true;
            controls.dampingFactor = 0.05;
            controls.screenSpacePanning = false;
            controls.minDistance = 10;
            controls.maxDistance = 500;

            // Postprocessing
            const composer = new EffectComposer(renderer);
            const renderPass = new RenderPass(scene, camera);
            composer.addPass(renderPass);
            const pass = new SMAAPass(window.innerWidth * renderer.getPixelRatio(), window.innerHeight * renderer.getPixelRatio());
            composer.addPass(pass);

            // Funkcja renderująca
            const render = () => {
                renderer.render(scene, camera);
            };

            // Główna pętla animacji
            const animate = () => {
                requestAnimationFrame(animate);

                if (typeof mixer !== 'undefined') {
                    mixer.update(clock.getDelta());
                }

                for (const model in mixers) {
                    mixers[model].update(clock.getDelta());
                }

                // Jeżeli którykolwiek klawisz jest wciśnięty
                if (Object.values(pressedKeys).includes(true)) {
                    const keysStateResult = checkKeysState();
                    objects['Robot'].position.add(keysStateResult.move);
                    objects['Robot'].rotation.y = keysStateResult.angle * (Math.PI / 180);
                }

                controls.update();
                //composer.render(); // Render postprocessingu
                render(); // Render standardowy
            };

            animate();
        })

        onUnmounted(() => {
            window.removeEventListener('keydown', keyDown);
            window.removeEventListener('keyup', keyUp);
        })

        return {camera, renderer, scene, clock, raycaster, objects, mixers, animations, pressedKeys, sceneRef, playAnimation, stopAnimation, testAllKeys, testAnyKeys, checkKeysState, robotRunning, keyUp, keyDown}
    }
}
