import { onUnmounted, ref, Ref } from 'vue';

export default function keysControls(pressedKeys: any) {

    const keysFunctions: Array<any> = [];
    const gamepads: Ref<(Gamepad | null)[]> = ref([]);

    const keyUp = (e: any) => {
        pressedKeys[e.code] = false;
    }

    const keyDown = (e: any) => {
        pressedKeys[e.code] = true;
    }

    const connectGamepad = (e: any) => {
        console.log(e.gamepad.id, 'podłączony')
    }

    const disconectGamepad = (e: any) => {
        console.log(e.gamepad.id, 'odłączony')
    }

    const checkPads = () => {
        gamepads.value = navigator.getGamepads()
    }

    const checkPressedKeys = () => {
        keysFunctions.forEach(func => { func() });
    }

    window.addEventListener('keydown', keyDown);
    window.addEventListener('keyup', keyUp);
    window.addEventListener("gamepadconnected", connectGamepad);
    window.addEventListener("gamepaddisconnected", disconectGamepad)

    const testAllKeys = (keysArray: any, exceptionKeysArray: any = null) => {
        const activeInArray = keysArray.every((e: any) => pressedKeys[e] === true); // Czy klawisze z listy są wciąnięte
        if (exceptionKeysArray) {
            const exceptionInArray = exceptionKeysArray.every((e: any) => pressedKeys[e] === false || pressedKeys[e] === undefined);
            return activeInArray && exceptionInArray;
        }
        return activeInArray;
    };

    const testAnyKeys = (keysArray: any) => {
        return keysArray.some((e: any) => pressedKeys[e] === true);
    };

    const testAllButtons = (buttonsArray: any, exceptionKeysArray: any = null) => {
        const activeInArray = buttonsArray.every((button: any) => gamepads.value[0]?.buttons[button].pressed)
        if (exceptionKeysArray) {
            const exceptionInArray = exceptionKeysArray.every((button: any) => !gamepads.value[0]?.buttons[button].pressed);
            return activeInArray && exceptionInArray;
        }
        return activeInArray;
    }

    const testAxis = () => {
        if(gamepads.value[0]) {
            const axisTolerance = 0.3
            const a0 = gamepads.value[0]?.axes[0] 
            const a1 = gamepads.value[0]?.axes[1]
            const angle = Math.atan2(a1, -a0) * 180 / Math.PI;

            const result = {
                angle: angle - 90,
                x: a0,
                z: a1
            }

            if(Math.abs(a0) > axisTolerance || Math.abs(a1) > axisTolerance) return result
        }
    }

    const testAnyButton = (buttonsArray: any) => {
        return buttonsArray.some((button: any) => gamepads.value[0]?.buttons[button].pressed);
    };

    onUnmounted(() => {
        window.removeEventListener('keydown', keyDown);
        window.removeEventListener('keyup', keyUp);
        window.removeEventListener("gamepadconnected", connectGamepad);
        window.addEventListener("gamepaddisconnected", disconectGamepad)
    })

    return {
        testAllKeys,
        testAnyKeys,
        keysFunctions,
        checkPressedKeys,
        testAllButtons,
        testAnyButton,
        testAxis,
        checkPads,
        gamepads
    }
}
