<template>
    <div class="container">
        <div class="preloader" v-show="loading.progress < 100">
            <div>
                Ładowanie
                <span v-show="loading.total">{{ loading.loaded }}/{{ loading.total }}</span>
            </div>
            <div v-show="loading.total">{{ loading.progress }}%</div>
        </div>

        <div class="lifes" v-if="lifes>0">
            <div v-for="(life, index) in lifes" class="life" :key="index"></div>
        </div>

        <div class="popup-wrapper" v-if="popup && loading.progress == 100">
            <div class="popup">
                <div class="title">Sterowanie</div>
                <div>WASD - chodzenie</div>
                <div>SHIFT - bieganie</div>
                <div>SPACE - skok</div>
                <!-- <div>Można także podłaczyć pada (testowane na padach z Xbox 360)</div> -->
                <button class="close" v-on:click="popup=false">X</button>
            </div>
        </div>

        <div v-if="gameOver" class="game-over">
            <div class="game-over-box">
                <div class="title">Koniec gry</div>
                <button @click="resetGame">Restart</button>
            </div>
        </div>

        <div class="scene" ref="sceneRef" :class="{'blur': loading.progress < 100}"></div>
    </div>
</template>

<script lang="ts">
import * as THREE from 'three';
import createScene from '../hooks/createScene';
import addLights from '../hooks/addLights';
import postProduction from '../hooks/postProduction';
import addMap from '../hooks/addMap';
import addCharacter from '../hooks/addCharacter';
import keysControls from '../hooks/keysControls';
import addLoaderManager from '../hooks/addLoaderManager';
import { publicPath } from '../../vue.config';

import { ref } from 'vue';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { Vector3 } from 'three';

//import * as dat from 'dat.gui';

export default {
    setup() {
        const popup = ref(false);
        const gameOver = ref(false)

        //const gui = new dat.GUI();
        // Zmienne sceny
        // const characterPosition = new Vector3(5, 11.5, 0);
        const characterPosition = new Vector3(0, 0, 0);
        //const cameraOffset = new Vector3(5, 30, 50); // wektor przesunięcia kamery względem postaci
        const cameraOffset = new Vector3(2.5, 15, 25);
        // const cameraOffset = new Vector3(-35, 15, 15);

        // Dodaj scenę
        const sceneRef = ref(null);
        const { scene, renderer, camera, animateFunctions, animation, controls } = createScene(sceneRef, characterPosition, cameraOffset);

        // Dodaj światła
        addLights(scene);

        // Dodaj postprodukcję
        postProduction(scene, camera, renderer)

        // Dodaj manager ładowania
        const manager = new THREE.LoadingManager();
        const { loading } = addLoaderManager(manager)

        // Załaduj główną mapę
        const loader = new GLTFLoader(manager);
        const objects: any = {};
        // addMap(loader, scene, objects, `${publicPath}peach_castle/scene.gltf`, `${publicPath}peach_castle/floor.gltf`);
        addMap(loader, scene, objects, `${publicPath}islands/islands.gltf`, `${publicPath}islands/islands_physics.gltf`);

        // Wczytaj funkcje obsługi klawiszy i padów
        const pressedKeys: any = {};

        const { testAllKeys, testAnyKeys, keysFunctions, checkPressedKeys, testAllButtons, testAnyButton, testAxis, checkPads, gamepads} = keysControls(pressedKeys);
        animateFunctions.push(checkPressedKeys, checkPads)

        // Wczytaj postać z funkcjami ruchu i animacji
        const animations: any = {};
        const mixers: any = {};
        const { robotRunning, toAnimate, lifes, resetGame } = addCharacter(
            loader, scene, objects, animations, mixers,
            pressedKeys, testAllKeys, testAnyKeys,
            gamepads, testAllButtons, testAnyButton, testAxis, `${publicPath}Robot_edited_centered.glb`,
            controls, camera, characterPosition, cameraOffset, animation, gameOver
        );
        animateFunctions.push(toAnimate);
        keysFunctions.push(robotRunning);

        return {
            sceneRef,
            loading,
            popup,
            close,
            camera,
            animations,
            mixers,
            lifes,
            gameOver,
            resetGame
        }
    }
}
</script>

<style scoped lang="scss">
.scene {
    width: 100vw;
    height: 100vh;
    transition: filter 0.3s;

    canvas {
        width: 100vw;
        height: 100vh;
        display: block;
    }
}

.info {
    z-index: 999;
    background-color: white;
    border: 1px solid black;
    position: absolute;
    right: 0;
    top: 0;
    text-align: left;
}

.preloader {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 998;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    font-weight: bold;
    color: white;
}

.blur {
    filter: blur(20px);
}

.popup-wrapper {
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    background: #ffffffcc;
    padding: 20px;
    border-radius: 10px;
    line-height: 1.5;
}

.popup {
    position: relative;

    .close {
        position: absolute;
        top: -10px;
        right: -10px;
        border: none;
        border-radius: 100%;
        padding: 5px;
        width: 30px;
        height: 30px;
        font-weight: bold;
        box-shadow: 0 0px 8px 0px rgb(189 183 183);
        cursor: pointer;
    }
}

.title {
    font-weight: bold;
    text-align: center;
    margin-bottom: 15px;
    text-transform: uppercase;
}

.lifes {
    position: fixed;
    display: flex;
    top: 0;
    right: 0;

    .life:before {
        content: '\2665';
        padding: 5px;
        font-size: 30px;
        color: red;
        text-shadow: 0px 1px 1px black;
    }
}

.game-over {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: black;
    display: flex;
    align-items: center;
    justify-content: center;

    &-box {
        background-color: white;
        padding: 20px;
        text-align: center;

        .title {
            font-size: 20px;
        }

        button {
            background: none;
            border: 2px solid black;
            padding: 5px 10px;
            font-size: 16px;
            cursor: pointer;
            transition: background 0.3s, color 0.3s;

            &:hover {
                background: black;
                color: white;
            }
        }
    }
}
</style>
